import {GET_STATES,DELETE_STATE,UPDATE_STATE,UPDATE_IMAGES,UPDATE_TAGS,ADD_OPTIONS,DELETE_IMAGES} from '../actions/types'
import Box from '@material-ui/core/Box';
const  initialState = {
    search: "",
    item_group_name: "",
    UOM: "",
    discription: "",
    images: "",
    option1: [],
    option2: [],
    option3: [],
    option4: [],
    option5: [],
      
};
    var array = initialState.option1;
    var index = array[initialState.option1.length - 1];
    array.splice(index,1)
export default function (state= initialState , action){

    switch(action.type){
        case GET_STATES:
        return{
         ...state
        }
        case UPDATE_STATE:
        return{
         ...state,
         [action.payload] : action.value
        }
        case DELETE_STATE:
        return{
         ...state,
         [action.payload] : action.value
        }
        case UPDATE_IMAGES:
        return{
         ...state,
         images : action.payload
        }
        case UPDATE_TAGS:
            return{
             ...state,
             [action.payload] :  action.value
            }
            case ADD_OPTIONS:
                return{
                 ...state,
                 [action.payload] : ""
                }
                case DELETE_IMAGES:
                return{
                 ...state,
                 images : ""
                }
            default:
            return state
    }
}