import {GET_STATES, UPDATE_STATE, DELETE_STATE,UPDATE_IMAGES,UPDATE_TAGS,ADD_OPTIONS,DELETE_IMAGES} from './types';

export const getStates=()=>{
    return {
        type:GET_STATES
    }
}
export const updateStates=(name,e)=>{
    return {
        type:UPDATE_STATE,
        payload:name,
        value:e.target.value,
    }
}
export const deleteStates=(name)=>{
    return {
        type:DELETE_STATE,
        payload:name,
    }
}
export const imagesUpdate=(target)=>{
    return {
        type:UPDATE_IMAGES,
        payload:target,
    }
}
export const updateTags=(name,val)=>{
    return {
        type:UPDATE_TAGS,
        payload:name,
        value:val,
    }
}
export const addOptions=(name)=>{
    return {
        type:ADD_OPTIONS,
        payload:name,
    }
}
export const deleteImages=()=>{
    return {
        type:DELETE_IMAGES,
    }
}
